<template>
    <div class="tel__info container">
        <RouterLink to="/About" class="info__img"> 
            <img :src="getTel.img" :alt="getTel.title">
        </RouterLink>
        
        <div class="tel__info_desc">
            <h2 class="tel__info_title">{{ getTel.title }}</h2>
            <p class="tel__info_text">{{ getTel.text }}</p>
        </div>
    </div>
</template>

<script>
import { items } from "@/items";
export default {
    data() {
        return {
            tels: items,
            currentId: null,
        }
    },
    created() {
        this.currentId = this.$route.params.id
    },

    computed: {
        getTel() {
            return this.tels.find(tel => tel.id == this.currentId)
        }
    }
}
</script>

